import React from 'react';
import s from './Music.module.css';

const Music = (props) => {
    if (props.tracks.length === 0) {
        props.setTracks([
            { id: 1, liked: true, artist: 'Drake', name: 'Major distribution', coverUrl: 'https://www.dailymaverick.co.za/wp-content/uploads/7aa1ef0ebfcab7c8bbe68a17f58ee8c0.jpg' },
            { id: 2, liked: false, artist: 'Face', name: 'Бургер', coverUrl: 'https://www.dailymaverick.co.za/wp-content/uploads/7aa1ef0ebfcab7c8bbe68a17f58ee8c0.jpg' },
            { id: 3, liked: false, artist: 'ЛСП', name: 'Тело', coverUrl: 'https://www.dailymaverick.co.za/wp-content/uploads/7aa1ef0ebfcab7c8bbe68a17f58ee8c0.jpg' },
            { id: 4, liked: true, artist: '9mice', name: 'Lipstick', coverUrl: 'https://www.dailymaverick.co.za/wp-content/uploads/7aa1ef0ebfcab7c8bbe68a17f58ee8c0.jpg' }
        ]);
    }
    return (
        <div className={s.wrapper}>
            {
                props.tracks.map(t => <div className={s.track} key={t.id}>
                    <img className={s.cover} src={t.coverUrl} />
                    <span>{t.artist}: </span>
                    <span>{t.name}</span><br />
                    {t.liked
                        ? <button onClick={() => props.dislike(t.id)} >Dislike</button>
                        : <button onClick={() => props.like(t.id)} >Like</button>}
                </div>)
            }
        </div>
    );
}

export default Music;